.nav-bar {
    background: #181818;
    width: 100%;
    height: 90px;
    position: relative;
    top: 0;
    z-index: 3;


    // min-height: 500px;
  
    // .logo {
    //   display: block;
    //   padding: 8px 0;
  
    //   img {
    //     display: block;
    //     margin: 8px auto;
    //     width: 24px;
    //     height: auto;
  
    //     &.sub-logo {
    //       width: 50px;
    //     }
    //   }
    // }
  
    nav {
      float: left;
      text-align: center;
      vertical-align: middle;
      position: relative;
      height: 80px;
      margin-top: 35px;
      width: 100%;
      font-family: 'Isabella Sans light';
      transform: translateX(2%);

      img {
        position: absolute;
        z-index: 1;
        display: inline;
        width: 150px;
        margin-top: -25px;
        margin-left: -120px;
      }
  
      a {
        font-size: 22px;
        color: #9a9898;
        display: inline;
        line-height: 20px;
        height: 20px;
        position: relative;
        text-decoration: none;
        padding: 50px;
  
        i {
          transition: all 0.3s ease-out;
        }
  
        &:hover {
          color: #cfc8c8;
  
          svg {
            opacity: 0;
          }
  
          &:after {
            opacity: 0;
          }
        }
  
        &:after {
          content: '';
          font-size: 25px;
          letter-spacing: 2px;
          position: absolute;
          bottom: 0;
          display: inline;
          width: 100%;
          text-align: center;
          opacity: 0;
          -webkit-transition: all 0.3s ease-out;
          transition: all 0.3s ease-out;
        }
  
        &:first-child {
          &:after {
            content: 'HOME';
          }
        }
      }
  
      a.about-link {
        &:after {
          content: 'ABOUT';
        }
      }
  
      a.contact-link {
        &:after {
          content: 'PUBLICATIONS';
        }
      }
  
      a.portfolio-link {
        &:after {
          content: 'RESUME';
        }
      }
  
      a.active {
        svg {
          color: #cfc8c8;
        }
      }
    }
  
  }
  
  .hamburger-icon, .close-icon {
    display: none;
  }
  
  
  @media screen and (max-width: 1200px) {
    .nav-bar { 
      background: transparent;
      position: initial;
      height: auto;
      min-height: auto;
  
       ul, nav {
        display: none;
       }
  
       nav {
        width: 100%;
        top: 0;
        left: 0;
        background: #181818;
        height: 100%;
        margin: 0;
        z-index: 2;
        position: fixed;
  
        &.mobile-show {
          display: block;
        }
  
        a {
          display: flex;
          justify-content: center;
          align-items: center;
        }
  
        a:after {
          opacity: 1;
          position: initial;
          width: auto;
          margin-left: 10PX;
        }
  
        a svg {
          opacity: 1 !important;
        }
       }
  
       .hamburger-icon,
       .close-icon {
          position: absolute;
          top: 15px;
          right: 15px;
          display: block;
       }
    }
  }